export default ()=> {
      return (
        <div className="question" data-index="72" dangerouslySetInnerHTML = {{ __html: `
<h6>72. 输出什么?</h6>
<pre class="language-javascript"><code class="language-javascript">console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span>String<span class="token punctuation">.</span>raw<span class="token template-string"><span class="token template-punctuation string">\`</span><span class="token string">Hello\nworld</span><span class="token template-punctuation string">\`</span></span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre>
<ul>
<li>A: <code>Hello world!</code></li>
<li>B: <code>Hello</code> <br />     <code>world</code></li>
<li>C: <code>Hello\nworld</code></li>
<li>D: <code>Hello\n</code> <br />      <code>world</code></li>
</ul>
<details><summary><b>答案</b></summary>
<p>
<h4>答案: C</h4>
<p><code>String.raw</code>函数是用来获取一个模板字符串的原始字符串的，它返回一个字符串，其中忽略了转义符（<code>\n</code>，<code>\v</code>，<code>\t</code>等）。但反斜杠可能造成问题，因为你可能会遇到下面这种类似情况：</p>
<pre class="language-javascript"><code class="language-javascript"><span class="token keyword keyword-const">const</span> path <span class="token operator">=</span> <span class="token template-string"><span class="token template-punctuation string">\`</span><span class="token string">C:\Documents\Projects\table.html</span><span class="token template-punctuation string">\`</span></span>
String<span class="token punctuation">.</span>raw<span class="token template-string"><span class="token template-punctuation string">\`</span><span class="token interpolation"><span class="token interpolation-punctuation punctuation">\${</span>path<span class="token interpolation-punctuation punctuation">}</span></span><span class="token template-punctuation string">\`</span></span>
</code></pre>
<p>这将导致：</p>
<p><code>&quot;C:DocumentsProjects able.html&quot;</code></p>
<p>直接使用<code>String.raw</code></p>
<pre class="language-javascript"><code class="language-javascript">String<span class="token punctuation">.</span>raw<span class="token template-string"><span class="token template-punctuation string">\`</span><span class="token string">C:\Documents\Projects\table.html</span><span class="token template-punctuation string">\`</span></span>
</code></pre>
<p>它会忽略转义字符并打印：<code>C:\Documents\Projects\table.html</code></p>
<p>上述情况，字符串是<code>Hello\nworld</code>被打印出。</p>
</p>
</details>
` }}></div>
      );
    }